.firelogo{
  height:50px;
  width:50px;
  animation-name: color_fire;
  animation-duration: 3s;
  animation-iteration-count: infinite;
  animation-direction:alternate;
}
.typelogo{
  height:50px;
  width:50px;
  animation-name: color_type;
  animation-duration: 3s;
  animation-iteration-count: infinite;
  animation-direction:alternate;
}
.hlogo{
  height:50px;
  width:50px;
  animation-name: color_heroku;
  animation-duration: 3s;
  animation-iteration-count: infinite;
  animation-direction:alternate;
}
.jslogo{
  height:50px;
  width:50px;
  animation-name: color_change;
  animation-duration: 3s;
  animation-iteration-count: infinite;
  animation-direction:alternate;
}
.csslogo{
  height:50px;
  width:50px;
  animation-name: color_shift;
  animation-duration: 3s;
  animation-iteration-count: infinite;
  animation-direction:alternate;
}
.htmllogo{
  height:50px;
  width:50px;
  animation-name: color_alter;
  animation-duration: 3s;
  animation-iteration-count: infinite;
  animation-direction:alternate;
}
.reactlogo{
  height:50px;
  width:50px;
  color:rgb(0, 140, 255);
  -webkit-animation:spin 3s linear infinite;
  -moz-animation: spin 3s linear infinite;
  animation:spin 3s linear infinite;
}
.reactlogo:hover{
  transform:scale(1.5);
}
.htmllogo:hover{
  transform:scale(1.5);
}
.csslogo:hover{
  transform:scale(1.5);
}
.jslogo:hover{
  transform:scale(1.5);
}
.typelogo:hover{
  transform:scale(1.5);
}
.firelogo:hover{
  transform:scale(1.5);
}
.hlogo:hover{
  transform:scale(1.5);
}
@-moz-keyframes spin { 100% { -moz-transform: rotate(360deg); } }
@-webkit-keyframes spin { 100% { -webkit-transform: rotate(360deg); } }
@keyframes spin { 100% { -webkit-transform: rotate(360deg); transform:rotate(360deg); } }
@-webkit-keyframes color_change{
  from{color: white;}
  to{color:yellow;}}
  @keyframes color_change{from{color: white;}
  to{color:yellow;}}
@-webkit-keyframes color_shift{
  from{color: white;}
  to{color:rgb(17, 0, 255);}}
  @keyframes color_shift{from{color: white;}
  to{color:rgb(0, 4, 255);}}
@-webkit-keyframes color_alter{
  from{color: white;}
  to{color:rgb(255, 94, 0);}}
  @keyframes color_alter{from{color: white;}
  to{color:rgb(255, 94, 0);}}
@-webkit-keyframes color_type{
  from{color: white;}
  to{color:rgb(4, 92, 255);}}
  @keyframes color_type{from{color: white;}
  to{color:rgb(4, 92, 255);}}
@-webkit-keyframes color_fire{
  from{color: white;}
  to{color:rgb(255, 33, 4);}}
  @keyframes color_fire{from{color: white;}
  to{color:rgb(255, 4, 4);}}
@-webkit-keyframes color_heroku{
  from{color: white;}
  to{color:rgb(96, 43, 182);}}
  @keyframes color_heroku{from{color: white;}
  to{color:rgb(96, 43, 182);}}